import React from 'react';
import NotFoundPage from '../HomePage/NotFoundPage';
import styles from '../FactopediaPage/factopedia.module.css';
import { list } from '../component/datalist.js';
import Aside from './aside.js'; 
import Footer from './pagefooter'; 

// the match helps the browser match the animal name with the parameter
const AnimalPages = ({ match }) => {
    const name = match.params.name;
    const animal = list.find(animal => animal.name === name);
    if (!animal) return <NotFoundPage />

    return (
        <>
            <div className={[styles.Container, styles.row_float_right].join(' ')}>
                <Aside />
                <main className={[styles.Main, styles.row_inner].join(' ')}>
                    <section className={[styles.Box, styles.box_expand].join(' ')} >
                        <h1>{animal.name}</h1>
                        <h1>{animal.other_name}</h1>
                        <h1>{animal.id}</h1>
                        <h1>{animal.weight_1}</h1>
                        <h1>{animal.weight_2}</h1>
                        <h1>{animal.height_1}</h1>
                        <h1>{animal.height_2}</h1>
                        <h1>{animal.coat}</h1>
                        <h1>{animal.color}</h1>
                        <h1>{animal.location}</h1>
                        <h1>{animal.animal_group}</h1>
                        <h1>{animal.offsprings}</h1>
                        <h1>{animal.edited_date}</h1>
                    </section>
                </main>
            </div>
            <Footer />
        </>
    );
}

export default AnimalPages;